@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css?family=Hind&display=swap);
body {
    background-color: #fff;
    color: #333;
    font-family: 'Arial';
    line-height: 1.8;
    margin: 0;
    padding: 0;
}

header a {
    text-decoration: none;
    color: black;
}

header {
    display: flex;
    justify-content: center;
    padding: 20px 0;
    background-color: #fff;
}

h1 {
    font-size: 2em;
    text-transform: uppercase;
    letter-spacing: 2px;
    position: relative; /* Add this line */
}

h1::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 60px;
    height: 2px;
    background-color: #001f3f; /* Dark blue color */
}

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    max-width: 1200px;
    margin: auto;
}

footer {
    display: flex;
    justify-content: center;
    padding: 20px 0;
    background-color: #fff;
}

footer a {
    display: inline-block;
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    text-decoration: none;
    font-weight: bold;
    border-radius: 3px;
    transition: background-color 0.3s ease;
}

footer a:hover {
    background-color: #444;
}

.main-page {
    text-align: center;
  }
  
  .main-page__title {
    font-size: 2rem;
    margin-bottom: 2rem;
  }
  
  .main-page__nav {
    display: flex;
    justify-content: center;
  }
  
  .main-page__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .main-page__item {
    margin: 1rem;
  }
  
  .main-page__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: black;
  }
  
  .main-page__image {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
  }
  
  .main-page__link-text {
    margin-top: 0.5rem;
    font-weight: bold;
  }
  
  .main-page__link:hover {
    background-color: #f5f5f5;
  }
  
.sign-in-wrapper {
  border: 1px solid #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 50px;
  margin: 20px auto;
  max-width: 1200px;
  /* height: 100vh; */
  background-color: #f2f2f2;
}
.sign-in-header {
    text-align: center;
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 30px;
}

.sign-in-header::after {
    content: "";
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 0;
    width: 60%;
    height: 1px;
    background: #333;
}

.title-container {
    margin-bottom: 20px;
}

.title {
    font-size: 2.5em;
    color: #333;
    font-weight: bold;
    text-align: center;
}

.error-message {
    color: red;
}

.sign-in-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    width: 100%;
    max-width: 400px;
}

.input-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
    position: relative;
}

.input-container::after {
    content: "";
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 0;
    width: 60%;
    height: 1px;
    background: #333;
}

.input-field {
    padding: 10px;
    font-size: 1em;
    width: 100%;
    border: none;
    border-bottom: 2px solid #333;
    background: transparent;
    transition: border-color 0.3s;
}

.input-field:focus {
    outline: none;
    border-color: #ddd;
}


.button-containter {
    display: flex;
    justify-content: center;
    width: 100%;
}

.submit-button {
    padding: 10px 20px;
    font-size: 1em;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 0.3s;
    width: 100%;
    max-width: 200px;
    text-align: center;
}

.submit-button:hover {
    background-color: #444;
}

.switch-button {
    background-color: transparent;
    border: 2px solid #333;
    color: #333;
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

.switch-button:hover {
    background-color: #333;
    color: #fff;
}

.input-label {
    font-size: 1.2em;
    color: #333;
    margin-bottom: 5px;
}
body {
    background-color: #fdf6e3;
    font-family: 'Helvetica Neue', Arial, sans-serif;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2em;
    color: #657b83;
    max-width: 800px;
    margin: auto;
    padding: 0 20px;
    box-sizing: border-box;
}

.form {
    display: flex;
    flex-direction: column;
    margin: 2em 0;
    width: 100%;
    box-sizing: border-box;
}

.input,
.textarea {
    padding: 10px;
    border-radius: 4px;
    border: 2px solid #93a1a1;
    font-size: 1em;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 20px;
    transition: border 0.3s ease;
}

.input:focus,
.textarea:focus {
    border-color: #586e75;
    outline: none;
}

.textarea {
    min-height: 150px;
    resize: vertical;
}

button {
    padding: 10px 20px;
    background-color: #2aa198;
    border: none;
    color: #fff;
    border-radius: 4px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-left: 10px; /* Add space between search input and button */
}

button:hover {
    background-color: #26867f;
}

.artwork {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 0;
    padding: 20px;
    border-radius: 4px;
    border: 2px solid #93a1a1;
    width: 100%;
    box-sizing: border-box;
}

h1, h2 {
    width: 100%;
    text-align: center;
    color: #586e75;
    margin: 20px 0;
}

h3, p {
    width: 100%;
    text-align: center;
    color: #657b83;
}

* {
  -webkit-font-smoothing: antialiased;
  color: #acbdce;
}

:root {
  --border-radius: 10px;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  padding: 20px;
}

.grid-item {
  border: 1px solid #ccc;
  padding: 10px;
  text-align: center;
  box-shadow: 2px 2px 6px rgba(0,0,0,0.1);
}

.artwork-image {
  max-width: 100%;
  max-height: 200px;
}

.Card {
  padding: 1px;
  border-radius: 10px;
  border-radius: var(--border-radius);
  background: linear-gradient(-67deg, rgba(200,216,231,0.7), rgba(255,255,255,0.8));
  overflow: hidden;
  box-shadow: -2px -2px 6px rgba(255,255,255,0.6), 2px 2px 12px rgba(200,216,231,1);
  width: 380px;
}

.CardInner {
  padding: 16px 16px;
  background-color: #e2e9f4;
  border-radius: 10px;
  border-radius: var(--border-radius);
}

.container {
  display: flex;
}

.Icon {
  min-width: 46px;
  min-height: 46px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  border-radius: var(--border-radius);
  margin-right: 12px;
  box-shadow: -2px -2px 6px rgba(255,255,255,0.6), 2px 2px 12px rgba(200,216,231,1);

  svg {
    -webkit-transform: translate(-1px, -1px);
            transform: translate(-1px, -1px);
  }
}

label {
  font-family: "Hind", sans-serif;
  display: block;
  color: #101620;
  margin-bottom: 12px;
  background: linear-gradient(45deg, rgba(107,123,143,1), #3c4b66);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.InputContainer {
  width: 100%;
}

input {
  background-color: #e3edf7;
  padding: 16px 32px;
  border: none;
  display: block;
  font-family: 'Orbitron', sans-serif;
  font-weight: 600;
  color: #a9b8c9;
  -webkit-appearance: none;
  transition: all 240ms ease-out;
  width: 100%;

  input::-webkit-input-placeholder {
    color: #6d7f8f;
  }

  input::placeholder {
    color: #6d7f8f;
  }

  input:focus {
    outline: none;
    color: #6d7f8f;
    background-color: lighten(#e3edf7, 3%);
  }
}

.search-form {
    display: flex;
    justify-content: center;
    margin-bottom: 2em;
}

.search-input {
    width: 40%;
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 5px;
    font-size: 16px;
    outline: none;
    transition: border-color 0.2s ease-in-out;
}

.search-input:focus {
    border-color: #007BFF;  /* Change this to the color you want when the input is focused */
}

.search-button {
    margin-left: 10px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    background-color: #007BFF;  /* Change this to your desired button color */
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
}

.search-button:hover {
    background-color: #0056b3;  /* Change this to your desired button hover color */
}

.results-title {
    text-align: center;
    font-size: 1.5em;  /* Adjust this to change the size of the title text */
    color: #0c2035;  /* Adjust this to change the color of the title text */
    padding: 20px 0;
    position: relative;
    overflow: hidden;
}

.results-title::after {
    content: '';
    display: block;
    width: 60%;  /* Adjust this to change the width of the line */
    height: 3px;  /* Adjust this to change the thickness of the line */
    background: #4287d1;  /* Adjust this to change the color of the line */
    position: absolute;
    left: 20%;  /* Adjust this along with width to center the line */
    bottom: 0;
}

.main-title {
    text-align: center;
    font-size: 1.5em;  /* Adjust this to change the size of the main title */
    color: #0a3461;  /* Adjust this to change the color of the main title */
    margin-bottom: 2em;
}

